.navbar {
  background-color: #f2f7fc;
  height: 100%;
  margin: 0;
  padding: 0;
  position: fixed;
  width: 10%; }
  .navbar h3 {
    color: #57b4c5;
    font-family: "Barlow";
    margin-bottom: 0;
    position: relative;
    top: 50%;
    -webkit-transform: translateY(-50%);
        -ms-transform: translateY(-50%);
            transform: translateY(-50%);
    -webkit-transition: font 0.3s ease;
    -o-transition: font 0.3s ease;
    transition: font 0.3s ease; }
  .navbar h5 {
    color: #57b4c5;
