.App {
  text-align: center;
  height: 100vh; }

a:link {
  color: #57b4c5;
  -webkit-text-decoration-line: none;
          text-decoration-line: none; }

a:visited,
a:hover {
  color: #f7c8a1; }

.section-wrapper.parallax {
  border-left: 1px solid #8dbdc4;
  height: 100vh;
  margin-left: 10%;
  overflow-x: hidden;
  overflow-y: auto;
  -webkit-perspective: 1px;
          perspective: 1px;
  perspective-origin-x: 100%; }

.parallax-layer {
  padding: 80vh 0;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  transform-origin-x: 100%;
  width: 100%; }

.layer-back {
  background-image: url("/assets/bg_top.png");
  background-attachment: fixed;
  background-size: 100% 100%;
  background-position-y: -15em;
  background-repeat: no-repeat;
  -webkit-transform: translateZ(-1px) scale(2.1);
          transform: translateZ(-1px) scale(2.1);
  z-index: 1;
  height: 50%;
  width: 100%; }

.layer-base {
  -webkit-transform: translateZ(0);
          transform: translateZ(0);
  padding-top: 56vh;
  z-index: 1; }

.section-bottom {
  background-attachment: fixed;
  background-image: url("/assets/bg_bottom.png");
  background-repeat: no-repeat;
  background-size: cover;
  -webkit-transform: translateZ(-5px) scale(6.1);
          transform: translateZ(-5px) scale(6.1);
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  width: 100%;
  z-index: 0; }



